<template>
  <div id="AutoManualGroup">
    <div class="leftMenu">
      <el-menu
        default-active="0-0-0"
        class="el-menu-vertical-demo"
        unique-opened
      >
        <el-submenu
          :index="index + ''"
          v-for="(item, index) in menuPlatFormList"
          :key="index"
        >
          <template slot="title">
            <span
              style="display:inline-block;width:100%"
              @click="onceClick(index)"
              >{{ item }}</span
            >
          </template>
          <el-submenu
            :index="index + '-' + index2"
            v-for="(item2, index2) in showCountryList"
            :key="index2"
          >
            <template slot="title">
              <span
                style="display:inline-block;width:100%"
                @click="secondClick(index2)"
                >{{ item2 }}</span
              >
            </template>
            <el-menu-item
              :index="index + '-' + index2 + '-' + index3"
              v-for="(item3, index3) in lastSiteList"
              :key="index3"
              @click="thirdClick(item, item2, item3)"
            >
              <span>{{ item3 }}</span>
            </el-menu-item>
          </el-submenu>
        </el-submenu>
      </el-menu>
    </div>
    <div class="right_box">
      <div class="topOpt">
        <el-button type="primary" size="small" @click="addNewdata" class="Btn"
          >新增</el-button
        >
        <el-button size="small" type="primary" @click="openedit" class="Btn"
          >编辑</el-button
        >
        <el-button type="danger" size="small" @click="deleteData" class="Btn"
          >删除</el-button
        >
        <el-button
          type="primary"
          size="small"
          @click="go_route('/AdSyncGroupControl')"
          class="Btn"
          >广告组合spu对应关系</el-button
        >
      </div>

      <el-table
        @select-all="selectAll"
        @select="select"
        ref="multipleTable"
        max-height="800"
        border
        :data="tableList"
        style="width: 1000"
      >
        <el-table-column type="selection" width="55"> </el-table-column>
        <el-table-column
          type="index"
          label="序号"
          :index="indexMethod"
          width="55"
          align="center"
        >
        </el-table-column>
        <el-table-column prop="country" width="80" label="国家">
        </el-table-column>
        <el-table-column prop="site" width="80" label="站点"> </el-table-column>
        <el-table-column prop="manual_name" width="300" label="手动组名称">
        </el-table-column>
        <el-table-column prop="automatic_name" width="380" label="自动组名称">
        </el-table-column>
      </el-table>
      <!-- <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="page"
        layout="total,prev, next, jumper"
        :total="total"
        :page-size="50"
        class="pagetotle"
      >
      </el-pagination> -->
      <!-- 弹框 -->
      <el-dialog
        title="新增"
        :visible.sync="DialogVisible"
        :close-on-click-modal="false"
        width="55%"
        center
      >
        <el-form
          ref="addFormDialog"
          :rules="addFormRules"
          :model="addForm"
          :inline="true"
          label-width="100px"
        >
          <el-form-item prop="country" label="国家">
            <el-input v-model="addForm.country" readonly></el-input>
          </el-form-item>
          <el-form-item label="站点" prop="site">
            <el-input v-model="addForm.site" readonly></el-input>
          </el-form-item>

          <el-form-item prop="manual_name" label="手动组名称">
            <el-input v-model="addForm.manual_name"></el-input>
          </el-form-item>
          <el-form-item prop="automatic_name" label="自动组名称">
            <el-input
              v-model="addForm.automatic_name"
              placeholder="多个spu请以英文,分割"
            ></el-input>
          </el-form-item>
          <el-form-item class="lastBtnGroup">
            <el-button type="primary" @click="addSubmit('addFormDialog')"
              >提 交</el-button
            >
            <el-button type="warning" @click="addReset">重 置</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>

      <el-dialog
        title="编辑"
        :visible.sync="DialogVisible1"
        :close-on-click-modal="false"
        width="20%"
        center
      >
        <el-form
          ref="editFormDialog"
          :rules="editFormRules"
          :model="editForm"
          :inline="true"
          label-width="100px"
        >
          <el-form-item prop="country" label="国家">
            <el-input v-model="editForm.country" readonly></el-input>
          </el-form-item>
          <el-form-item label="站点" prop="site">
            <el-input v-model="editForm.site" readonly></el-input>
          </el-form-item>

          <el-form-item prop="manual_name" label="手动组名称">
            <el-input v-model="editForm.manual_name"></el-input>
          </el-form-item>
          <el-form-item prop="automatic_name" label="自动组名称">
            <el-input v-model="editForm.automatic_name"></el-input>
          </el-form-item>
          <el-form-item class="lastBtnGroup">
            <el-button type="primary" @click="editSubmit('editFormDialog')"
              >保 存</el-button
            >
            <el-button type="warning" @click="editReset">重 置</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
      <el-dialog title="提示" :visible.sync="deleteVisible" width="30%">
        <span>确认删除？</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="deleteVisible = false">取 消</el-button>
          <el-button type="primary" @click="deleteSubmit">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 新增 编辑标志
      flag: false,
      // 新增表格验证
      DialogVisible: false,
      addFormRules: {
        country: [{ required: true, message: "请输入国家", trigger: "blur" }],
        site: [{ required: true, message: "请输入站点", trigger: "blur" }],
        manual_name: [
          { required: true, message: "请输入手动组名称", trigger: "blur" }
        ],
        automatic_name: [
          { required: true, message: "请输入自动组名称", trigger: "blur" }
        ]
      },
      addForm: {
        country: "",
        site: "",
        manual_name: "",
        automatic_name: ""
      },
      // 编辑表格验证
      DialogVisible1: false,
      editFormRules: {
        country: [{ required: true, message: "请输入国家", trigger: "blur" }],
        site: [{ required: true, message: "请输入站点", trigger: "blur" }],
        manual_name: [
          { required: true, message: "请输入手动组名称", trigger: "blur" }
        ],
        automatic_name: [
          { required: true, message: "请输入自动组名称", trigger: "blur" }
        ]
      },
      editForm: {
        id: "",
        country: "",
        site: "",
        manual_name: "",
        automatic_name: ""
      },

      // 左侧菜单
      menuPlatFormList: [],
      showCountryList: [],
      menuSiteList: [],
      // 默认显示第一个
      showCountryList: "",
      showSiteList: "",
      lastSiteList: "",
      // ？？？
      checkedPlatform: "",
      checkedCountry: "",
      checkedSite: "",

      lastSiteList: [],
      // 右侧表格
      tableList: [
        {
          xx: 112
        },
        {
          xx: 3334
        }
      ],
      // 选中行
      multipleSelection: [],
      // 分页：
      // page: 1,
      // total: 0,
      // 删除
      id: "",
      deleteVisible: false
    };
  },
  created() {
    this.getMenuList();
  },
  methods: {
    deleteSubmit() {
      this.deleteVisible = false;
      this.$api2
        .AMdeleteFormData({
          id: this.id
        })
        .then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.$message.success("请求成功");
            this.multipleSelection = [];
            this.getTableData();
          } else {
            this.$message.warning("请求失败");
          }
        });
    },
    go_route(val) {
      this.$router.push({ path: val });
    },
    // 选中行
    //勾选单行操作
    select(selection, row) {
      this.multipleSelection = []; //清空已选
      // 选择项大于1时
      if (selection.length > 1) {
        let del_row = selection.shift();
        // console.log('把数组的第一个元素从其中删除后', selection);
        this.$refs.multipleTable.toggleRowSelection(del_row, false); // 用于多选表格，切换某一行的选中状态，如果使用了第二个参数，则是设置这一行选中与否（selected 为 true 则选中）
      }
      this.multipleSelection.push(selection[0]);
      console.log("当用户手动勾选数据行", this.multipleSelection);
    },

    //全选操作
    selectAll(selection) {
      this.multipleSelection = []; //清空已选
      console.log("当用户手动勾选全选 Checkbox 时触发的事件", selection);
      // 选择项大于1时
      if (selection.length > 1) {
        selection.length = 1;
        this.multipleSelection.push(selection[0]);
      } else {
        this.multipleSelection.push(selection[0]);
      }
      console.log("当用户手动勾选全选", this.multipleSelection);
    },

    openedit() {
      if (!this.flag) return;
      if (
        this.multipleSelection.length === 0 ||
        (this.multipleSelection.length === 1 &&
          this.multipleSelection[0] === undefined)
      ) {
        this.$message.warning("请选择一个数据进行编辑");
        return;
      }
      this.DialogVisible1 = true;
      for (let key in this.editForm) {
        this.editForm[key] = "";
      }
      let curItem = this.multipleSelection[0];
      console.log("当前选中的对象", curItem);
      for (let key in this.editForm) {
        this.editForm[key] = curItem[key];
      }
    },
    addNewdata() {
      //打开新增弹框操作
      if (!this.flag) return;
      this.DialogVisible = true;
      for (let key in this.addForm) {
        this.addForm[key] = "";
      }
      this.addForm["country"] = this.checkedCountry;
      this.addForm["site"] = this.checkedSite;
    },
    deleteData() {
      //删除操作
      // 弹框确认
      if (
        this.multipleSelection.length === 0 ||
        (this.multipleSelection.length === 1 &&
          this.multipleSelection[0] === undefined)
      ) {
        this.$message.warning("请选择一个数据删除");
        return;
      }

      this.id = this.multipleSelection[0].id;
      console.log(this.id);
      this.deleteVisible = true;
    },
    addSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (!valid) return;
        this.DialogVisible = false;
        let fd = new FormData();
        for (let key in this.addForm) {
          fd.append(key, this.addForm[key]);
        }
        this.$api2.AMAddSubmit(fd).then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.$message.success("请求成功");
            // 刷新
            this.getTableData();
          } else {
            this.$message.warning("请求不成功");
          }
        });
      });
    },
    addReset() {
      for (let key in this.addForm) {
        console.log(key);
        if (key !== "country" && key !== "site") {
          this.addForm[key] = "";
        }
      }
    },
    editSubmit(form) {
      this.$refs[form].validate(valid => {
        if (!valid) return;
        this.DialogVisible1 = false;
        let fd = new FormData();
        for (let key in this.editForm) {
          fd.append(key, this.editForm[key]);
        }
        this.$api2.AMeditFormData(fd).then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.$message.success("请求成功");

            this.getTableData();
            // 重新请求，选中数组zhi为空
            this.multipleSelection = [];
          } else {
            this.$message.warning("请求失败");
          }
        });
      });
    },
    editReset() {
      for (let key in this.editForm) {
        if (key !== "country" && key !== "site" && key !== "id") {
          this.editForm[key] = "";
        }
      }
    },
    indexMethod(val) {
      return val + 1;
    },
    // 分页：
    // handleCurrentChange(val) {
    //   this.page = val;
    //   // 数据请求
    // },
    // 获取菜单栏数据
    getMenuList() {
      this.$api.getMenuList().then(res => {
        // console.log(res);
        this.menuPlatFormList = res.data.data1;
        this.menuCountryList = res.data.data2;
        this.menuSiteList = res.data.data3;
        // 页面刷新 先默认展示胤佑的 同时在导航那里添加default-active="0-0-0"
        this.showCountryList = this.menuCountryList[0];
        this.showSiteList = this.menuSiteList[0];
        this.lastSiteList = this.showSiteList[0];
        this.checkedPlatform = this.menuPlatFormList[0];
        this.checkedCountry = this.showCountryList[0];
        this.checkedSite = this.lastSiteList[0];
        this.thirdClick(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite
        );
      });
    },
    onceClick(index) {
      // if(this.platformOnce[index] == "") {
      this.showCountryList = [];
      this.showSiteList = [];
      this.showCountryList = this.menuCountryList[index];
      this.showSiteList = this.menuSiteList[index];
      // }
    },
    secondClick(index) {
      // console.log(index);
      this.lastSiteList = [];
      this.lastSiteList = this.showSiteList[index];
    },
    getTableData() {
      // 请求数据
      this.$api2
        .AMgetTableData({
          country: this.checkedCountry,
          site: this.checkedSite
        })
        .then(res => {
          console.log(res);
          this.tableList = res.data.re_data || [];
        });
    },
    thirdClick(platform, country, site) {
      console.log("渠道站点国家", platform, country, site);
      // 选择渠道站点国家后请求数据
      this.checkedCountry = country;
      this.checkedSite = site;
      this.flag = true;
      this.getTableData();
    }
  }
};
</script>
<style>
.el-pagination {
  background-color: #fff;
  margin: 0 auto;
  width: 600px;
  text-align: center;
}
</style>
<style scoped>
#AutoManualGroup {
  position: relative;
  overflow: hidden;
  height: 92%;
}
#AutoManualGroup .el-menu {
  max-height: 87vh;
  overflow-y: auto;
}
#AutoManualGroup .leftMenu {
  width: 250px;
  position: absolute;
  left: 0;
  top: 0;
  background: rgba(255, 255, 255, 0.9);
}
#AutoManualGroup .right_box {
  margin-left: 30vw;
  width: 50vw;
  max-height: 87vh;
}
#AutoManualGroup .topOpt {
  background: rgba(355, 255, 255, 0.9);
  padding: 10px 20px;
}
</style>
<style>
#AutoManualGroup .el-pagination {
  background: rgba(255, 255, 255, 0.9);
  text-align: center;
}
</style>



